.overlay {
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
  pointer-events: none;
  z-index: 15;
  background-color: rgba(0, 0, 0, .6);
  justify-content: center;
  align-items: center;
  animation: hide 2.5s linear forwards;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.overlay:after {
  content: "";
  width: 300px;
  height: 100px;
  background-image: url("logo.8f142ad4.svg");
  background-repeat: no-repeat;
  background-size: contain;
  animation: show .5s;
  display: block;
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, 50%);
}

.loader {
  justify-content: space-between;
  align-items: center;
  gap: 25px;
  animation: show .5s;
  display: flex;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, 50%);
}

.dot {
  width: 18px;
  height: 18px;
  background-color: #ffd723;
  border-radius: 50%;
  animation: bounce .6s infinite alternate;
}

.dot:nth-child(2) {
  animation-delay: .2s;
}

.dot:nth-child(3) {
  animation-delay: .4s;
}

@keyframes bounce {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(-15px);
  }
}

@keyframes hide {
  0% {
    -webkit-backdrop-filter: blur(2px);
    backdrop-filter: blur(2px);
    display: block;
  }

  60% {
    opacity: 1;
  }

  100% {
    -webkit-backdrop-filter: blur();
    backdrop-filter: blur();
    opacity: 0;
    display: none;
  }
}

@keyframes show {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@media (min-width: 480px) {
  .overlay:after {
    width: 400px;
  }
}

