.burger-container {
  width: 42px;
  height: 42px;
  cursor: pointer;
  z-index: 10;
  background-color: #eef0e7;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  padding: 14px 11px;
  animation: hideBorder .5s;
  display: flex;
  position: relative;
  transform: scale(1);
}

.burger-container.active {
  border: 1px solid #000;
  transition: transform .5s;
  animation: showBorder .5s;
}

@media (hover: none) {
  .burger-container.active:active {
    transform: scale(.9);
  }
}

.burger {
  width: 18px;
  height: 12.6px;
  padding: 0;
  position: relative;
}

.burger .burger__stick {
  height: 2px;
  width: 100%;
  background-color: #000;
  transition: transform .3s;
  display: block;
}

.burger.active .burger__stick {
  transform: scale(0);
}

.burger:before, .burger:after {
  content: "";
  height: 2px;
  width: 100%;
  background-color: #000;
  transition: transform .3s;
  display: block;
  position: absolute;
}

.burger:before {
  top: 0;
}

.burger:after {
  bottom: 0;
}

.burger.active:before {
  top: 50%;
  transform: rotate(45deg)translateY(-50%);
}

.burger.active:after {
  bottom: 50%;
  transform: rotate(-45deg)translateY(50%);
}

