@media (min-width: 768px) {
  .burger-container {
    width: 40px;
    height: 40px;
    border: none;
    animation: none;
  }

  .burger-container.active {
    border: none;
    animation: none;
  }

  .burger-container.active:hover {
    transform: none;
  }

  .nav__menu {
    inset: inherit;
    width: 250px;
    background-color: #eef0e7;
    border-radius: 30px;
    padding: 30px 50px 30px 30px;
    animation: hideDesctop .6s;
    position: absolute;
    top: -3px;
    right: -3px;
    transform: translateY(-120%);
  }

  .nav__menu.active {
    animation: showDesctop .6s;
    transform: translateY(0);
  }

  .menu__item {
    font-size: 22px;
  }

  .menu__link {
    color: #000;
  }

  .menu__link:hover {
    color: #ffd723;
  }
}

@keyframes showDesctop {
  from {
    display: none;
    transform: translateY(-120%);
  }

  to {
    display: flex;
    transform: translateY(0);
  }
}

@keyframes hideDesctop {
  from {
    display: flex;
    transform: translateY(0);
  }

  to {
    display: none;
    transform: translateY(-120%);
  }
}

@keyframes showMobile {
  from {
    opacity: 0;
    display: none;
  }

  to {
    opacity: 1;
    display: flex;
  }
}

@keyframes hideMobile {
  from {
    opacity: 1;
    display: flex;
  }

  to {
    opacity: 0;
    display: none;
  }
}

@keyframes showBorder {
  from {
    border: 1px solid rgba(0, 0, 0, 0);
  }

  to {
    border: 1px solid #000;
  }
}

@keyframes hideBorder {
  from {
    border: 1px solid #000;
  }

  to {
    border: 1px solid rgba(0, 0, 0, 0);
  }
}

